import React, { useState } from 'react';

const DataEntryForm = ({ onSubmit }) => {
  const [formData, setFormData] = useState({
    place: '',
    wellName: '',
    status: 'Active',
    qg: '',
    qc: '',
    qw: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.place || !formData.wellName || !formData.qg) {
      setError("Please fill all required fields.");
      return;
    }

    setIsSubmitting(true);
    setError('');

    try {
      const response = await fetch('http://localhost:3001/api/save-well-data', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (data.success) {
        onSubmit(formData); // Pass form data to parent component
        setFormData({
          place: '',
          wellName: '',
          status: 'Active',
          qg: '',
          qc: '',
          qw: ''
        });
      } else {
        setError('Failed to save data. Please try again.');
      }
    } catch (err) {
      setError('Error connecting to server. Please try again.');
      console.error('Error submitting form:', err);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="max-w-xl mx-auto bg-white p-6 rounded shadow-md space-y-4">
      <h2 className="text-2xl font-bold mb-4">Oil Well Data Entry</h2>

      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative" role="alert">
          <span className="block sm:inline">{error}</span>
        </div>
      )}

      <div>
        <label className="block font-medium">Place</label>
        <input
          type="text"
          name="place"
          value={formData.place}
          onChange={handleChange}
          className="w-full border p-2 rounded"
          required
        />
      </div>

      <div>
        <label className="block font-medium">Well Name</label>
        <input
          type="text"
          name="wellName"
          value={formData.wellName}
          onChange={handleChange}
          className="w-full border p-2 rounded"
          required
        />
      </div>

      <div>
        <label className="block font-medium">Status</label>
        <select
          name="status"
          value={formData.status}
          onChange={handleChange}
          className="w-full border p-2 rounded"
        >
          <option>Active</option>
          <option>Inactive</option>
          <option>Under Maintenance</option>
        </select>
      </div>

      <div>
        <label className="block font-medium">Qg (Gas, MMSCMD)</label>
        <input
          type="number"
          step="any"
          name="qg"
          value={formData.qg}
          onChange={handleChange}
          className="w-full border p-2 rounded"
          required
        />
      </div>

      <div>
        <label className="block font-medium">Qc (Condensate, m/day)</label>
        <input
          type="number"
          step="any"
          name="qc"
          value={formData.qc}
          onChange={handleChange}
          className="w-full border p-2 rounded"
        />
      </div>

      <div>
        <label className="block font-medium">Qw (Water, m/day)</label>
        <input
          type="number"
          step="any"
          name="qw"
          value={formData.qw}
          onChange={handleChange}
          className="w-full border p-2 rounded"
        />
      </div>

      <button
        type="submit"
        disabled={isSubmitting}
        className={`w-full bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 transition ${
          isSubmitting ? 'opacity-50 cursor-not-allowed' : ''
        }`}
      >
        {isSubmitting ? 'Saving...' : 'Submit'}
      </button>
    </form>
  );
};

export default DataEntryForm;
