import React from 'react';
import { Link, useLocation } from 'react-router-dom';

const Header = () => {
  const location = useLocation();

  return (
    <div className="bg-gradient-to-r from-blue-600 to-blue-800 text-white shadow-lg">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          <div className="flex items-center">
            <h1 className="text-2xl font-bold">Oil Well Monitoring Dashboard</h1>
          </div>
          <div className="flex space-x-4">
            <Link
              to="/dashboard"
              className={`px-3 py-2 rounded-md text-sm font-medium ${
                location.pathname === '/dashboard'
                  ? 'bg-blue-900 text-white'
                  : 'text-blue-100 hover:bg-blue-700'
              }`}
            >
              Dashboard
            </Link>
            <Link
              to="/data-entry"
              className={`px-3 py-2 rounded-md text-sm font-medium ${
                location.pathname === '/data-entry'
                  ? 'bg-blue-900 text-white'
                  : 'text-blue-100 hover:bg-blue-700'
              }`}
            >
              Data Entry
            </Link>
            <Link
              to="/well-map"
              className={`px-3 py-2 rounded-md text-sm font-medium ${
                location.pathname === '/well-map'
                  ? 'bg-blue-900 text-white'
                  : 'text-blue-100 hover:bg-blue-700'
              }`}
            >
              Well Map
            </Link>
            <button
              onClick={() => {
                localStorage.removeItem('isAuthenticated');
                window.location.href = '/login';
              }}
              className="px-3 py-2 rounded-md text-sm font-medium text-blue-100 hover:bg-blue-700"
            >
              Logout
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header; 